import React, { Component } from 'react';
import {
  FormGroup,
  FormText,
  FormInline,
  FormCheck,
  FormCheckInput,
  Label,
  FormControl,
  Container,
  Button,
} from 'styled-bootstrap-components';
import styled from 'styled-components';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import {
  withRouter, Link,
} from 'react-router-dom';
import PropTypes from 'prop-types';
import { Field, reduxForm } from 'redux-form';
import * as widgets from '../widgets';
import { addWidget } from '../actions/dashboard';
import { getDashboard } from '../selectors/dashboard';
import SelectField from '../form/SelectField';
import Widgets from './widgets.jsx';
import widgetsJSON from './widgets.json';


const {
  number, array, func, object,
} = PropTypes;

const ButtonContainer = styled.div`
  &div {
    margin: 10px;
  }
`;
const RadioContainer = styled.div`
& input[type="radio"] {
  opacity: 0;
  position: fixed;
  width: 0;

}
& input[type="radio"] + label {
  padding: 10px;
}

& input[type="radio"]:checked + label {
  background-color:rgba(0,0,255,.3);
  background-color: #007bff;
  border-color: #007bff;
  color: white;

}
& form > div {
  margin: 10px 0px;
}
`;
const renderField = field => (
  <div className="input-row">
    <FormControl

      {...field.input}
    />

  </div>
);

const renderWidgetChoice = (field) => {
  const { input: { value = 'choiceB', onChange }, defaultChecked } = field;
  const v = value || defaultChecked;
  return (
    <>
      <RadioContainer>
        <FormInline style={{ color: 'black', justifyContent: 'space-around' }}>
          <FormGroup>
            <FormCheck>
              <FormCheckInput
                id="choiceA"
                type="radio"
                name="selectType"
                onClick={() => {
                  onChange('choiceA');
                }}
                checked={v === 'choiceA'}
              />
              <label htmlFor="choiceA">Choose A Widget</label>
            </FormCheck>
          </FormGroup>
          <FormGroup>
            <FormCheck>
              <FormCheckInput
                id="choiceB"
                type="radio"
                name="selectType"
                checked={v === 'choiceB'}
                onClick={() => {
                  onChange('choiceB');
                }}
              />
              <label htmlFor="choiceB">Custom Widget</label>
            </FormCheck>
          </FormGroup>
        </FormInline>
      </RadioContainer>
      { value === 'choiceB' && <>
        <FormGroup>
          <Label>Name</Label>
          <Field name="name" component={renderField} type="text" />
        </FormGroup>
        <FormGroup>
          <Label>Logo</Label>
          <Field name="logo" component={renderField} type="text" />
        </FormGroup>
        <FormGroup>
          <Label>URL</Label>
          <Field name="url" component={renderField} type="text" />
        </FormGroup>
          </>}
      {value === 'choiceA' && <>        <Field name="choiceAWidgets" component={Widgets} type="checkbox" /></>}
          </>

  );
};
const widgetTypes = Object.keys(widgets);
class AddWidget extends Component {
  static defaultProps = {

  };

  onAdd(values) {
    // TODO: show modal?c

    const { widgetChoice, choiceAWidgets } = values;
    const {
      newCounter, items, addWidget, history,
    } = this.props;
    if (widgetChoice === 'choiceA') {
      choiceAWidgets.forEach((widget) => {
        addWidget({
          i: `${widget}`,
          x: (items.length * 2) % (12),
          y: Infinity, // puts it at the bottom
          w: 2,
          h: 2,
          widgetChoice,
          values: widgetsJSON.widgets.find(({ text }) => text === widget),
        });
      });
    } else {
      addWidget({
        i: `n${newCounter}`,
        x: (items.length * 2) % (12),
        y: Infinity, // puts it at the bottom
        w: 2,
        h: 2,
        widgetChoice,
        values,
      });
    }

    history.push('/');
  }


  render() {
    const { handleSubmit } = this.props;
    return (
      <Container fluid style={{ color: 'black' }}>
        {/*
        <FormGroup>
          <Label>Widget Type</Label>
          <Field
            name="widgetType"
            component={SelectField}
            options={[
              'Select Type',
              ...widgetTypes,
            ]}
          />
        </FormGroup>
        */}
        <Field name="widgetChoice" component={renderWidgetChoice} onChange={e => console.log(e)} />

        <ButtonContainer>
          <Button primary onClick={handleSubmit(values => this.onAdd(values))}>Add</Button>
          <Link to="/">
            <Button primary style={{ margin: 10 }}>Cancel</Button>
          </Link>
        </ButtonContainer>

      </Container>
    );
  }
}

AddWidget.propTypes = {
  newCounter: number.isRequired,
  items: array.isRequired,
  addWidget: func.isRequired,
  handleSubmit: func.isRequired,
  history: object.isRequired,
};

const mapStateToProps = state => ({
  ...getDashboard(state),
  initialValues: {
    widgetChoice: 'choiceA',
  },
});

const mapDispatchToProps = dispatch => bindActionCreators({
  addWidget,
}, dispatch);
export default connect(mapStateToProps, mapDispatchToProps)(reduxForm({
  form: 'addWidget',
})(withRouter(AddWidget)));
