import { ADD_WIDGET, REMOVE_WIDGET, UPDATE_LAYOUT } from '../constants';

let fromLocalStorage = {
};
try {
  fromLocalStorage = JSON.parse(localStorage.getItem('dashboard'));
  fromLocalStorage.items = fromLocalStorage.items.map(item => ({
    ...item,
    y: item.y === null ? Infinity : item.y,
  }));
} catch (err) {
  //
}

const initialState = {
  items: [],
  newCounter: 0,
  className: 'layout',
  cols: {
    lg: 12, md: 10, sm: 6, xs: 4, xxs: 2,
  },
  rowHeight: 100,
  ...fromLocalStorage,
};
const actionHandlers = {
  [ADD_WIDGET]: (state, { payload }) => {
    const { items, newCounter } = state;
    const newState = {
      ...state,
      items: [...items, payload],
      newCounter: newCounter + 1,
    };
    localStorage.setItem('dashboard', JSON.stringify(newState));
    return newState;
  },
  [REMOVE_WIDGET]: (state, { payload: i }) => {
    const { items } = state;
    const newState = {
      ...state,
      items: items.filter(item => item.i !== i),
    };
    localStorage.setItem('dashboard', JSON.stringify(newState));
    return newState;
  },
  [UPDATE_LAYOUT]: (state, { layout }) => {
    const { items } = state;
    const newState = {
      ...state,
      items: items.map((item, index) => {
        const {
          x, y, w, h,
        } = layout[index];
        return {
          ...item,
          x,
          y,
          w,
          h,
        };
      }),
    };
    localStorage.setItem('dashboard', JSON.stringify(newState));
    return newState;
  },
};

export default (state = initialState, action) => {
  const actionHandler = actionHandlers[action.type];
  if (actionHandler) {
    return actionHandler(state, action);
  }
  return state;
};
