import { Responsive, WidthProvider } from 'react-grid-layout';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import React, { PureComponent } from 'react';
import { library } from '@fortawesome/fontawesome-svg-core';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faMinusCircle, faArrowsAlt, faEdit } from '@fortawesome/free-solid-svg-icons';
import { Link } from 'react-router-dom';

import {
  Card,
  CardBody,
  // CardFooter,
  // CardHeader,
  // CardImageHeader,
  // CardText,
  CardTitle,
  Button,
  Container,
} from 'styled-bootstrap-components';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { removeWidget, updateLayout } from './actions/dashboard';
import './App.css';
import 'react-grid-layout/css/styles.css';
import 'react-resizable/css/styles.css';
import { getDashboard } from './selectors/dashboard';
import Modal from './modal';
import AddItem from './containers/AddItem';

let TheatreMode = false;

if (document.referrer.match(/^https:\/\/www\.youtube\.com/)) {
  TheatreMode = true;
}

library.add(faMinusCircle);
library.add(faArrowsAlt);
library.add(faEdit);

const {
  number, array, func, string,
} = PropTypes;


const ResponsiveGridLayout = WidthProvider(Responsive);
const Cell = styled.div`
  font-family: 'Gotham Medium';
  font-size: 1.5em;
  text-align: center;
  padding: 2px;
  overflow: hidden;
`;

const RemoveBtn = styled.span`
  position: absolute;
  right: 6px;
  top: 0;
  cursor: pointer;
  color: rgba(190,5,5,1);
  opacity: .1;
    transition: opacity .5s ease-in;

  &:hover {
    color: rgba(190,5,5,1);
    opacity: 1
  }
`;
const ButtonContainer = styled.div`
  positionx: ${({ editMode }) => (editMode ? 'static' : 'absolute')};
  position: absolute;
  left: 10px;
  top: 10px;
  display: flex;
  flex-direction: column-reverse;
  background: ${({ editMode }) => (editMode ? 'rgba(0,0,0,.1)' : 'none')};
  padding:  ${({ editMode }) => (editMode ? '20px' : '0')};
  &> * {
    margin: 5px;
  }
`;

const AppDiv = styled.div`
  background: ${({ editMode }) => (editMode ? 'white' : 'black')};
  color: ${({ editMode }) => (editMode ? 'black' : 'white')};

`;

const showModal = {
  addItem: () => (
    <Modal title="Add Widget">
      <AddItem />
    </Modal>
  )
  ,
};
const WidgetCardStyle = styled.a`
    display: block;
    background-size: contain;
    background-repeat: no-repeat;
`;
const WidgetCard = ({ i, widgetChoice, values }) => {
  if (widgetChoice === 'choiceA') {
    const { text, url, logo } = values;
    return (
      <WidgetCardStyle
        href={url}
        style={{
          width: '100%',
          height: '100%',
          backgroundImage: `url("${logo}")`,
        }}
      />
    );
  }
  return (<CardTitle>{i}</CardTitle>);
};
class App extends PureComponent {
  static defaultProps = {
    className: 'layout',
    cols: {
      lg: 12, md: 10, sm: 6, xs: 4, xxs: 2,
    },
    rowHeight: 100,
    show: '',
  };

  constructor(props) {
    super(props);
    this.state = {
      editMode: false,
    };
  }

  onRemove(i) {
    const {
      removeWidget,
    } = this.props;
    removeWidget(i);
  }

  render() {
    const {
      items, rowHeight, className, show, updateLayout,
    } = this.props;
    const { editMode } = this.state;

    const Modal = showModal[show];
    return (
      <AppDiv className="App" editMode={editMode}>
        <h1>Tesla Screen</h1>

        <ResponsiveGridLayout
          className={className}
          isResizable={editMode}
          isDraggable={editMode}
          onLayoutChange={layout => updateLayout(layout)}

          breakpoints={{
            lg: 1200, md: 996, sm: 768, xs: 480, xxs: 0,
          }}
          cols={{
            lg: 12, md: 10, sm: 6, xs: 4, xxs: 2,
          }}
          rowHeight={rowHeight}
        >
          {
          items.map(el => (
            <Cell key={el.i} data-grid={el}>
              <Card bsStyle="primary" style={{ background: 'rgba(0,0,0,0)' }}>
                <CardBody>
                  <WidgetCard {...el} />
                </CardBody>
              </Card>
              { editMode && (
              <RemoveBtn onClick={() => this.onRemove(el.i)}>
                <FontAwesomeIcon icon="minus-circle" size="xs" />
              </RemoveBtn>
              )
  }

            </Cell>
          ))
        }

          {/*
         <Cell key="a">Stock Ticker</Cell>
          <Cell key="b">Camera</Cell>
          <Cell key="c">Near By Events</Cell>
          <Cell key="d">Chat</Cell>
          <Cell key="e">Share your location</Cell>
          <Cell key="f">Favorite food near by</Cell>
          <Cell key="g">TODO List</Cell>
          <Cell key="h">Calculator</Cell>
          */}

        </ResponsiveGridLayout>
        <Container fluid style={{ backgroundColor: 'rgba(0,0,0,0)', color: 'white', padding: '10px' }}>
          <ButtonContainer editMode={editMode}>

            { editMode && (
            <Link to="/addItem">
              <Button>Add Widget</Button>
            </Link>
            )}

            { !TheatreMode && editMode && (
            <a href="https://www.youtube.com/redirect?q=http://teslascreen.navto.net/">
              <Button>Full Screen</Button>
            </a>
            )}
            <Button

              onClick={() => {
                this.setState({
                  editMode: !editMode,
                });
              }}
            >
              {editMode ? 'Done' : <FontAwesomeIcon icon="edit" size="xs" /> }
            </Button>
          </ButtonContainer>
        </Container>
        {Modal && <Modal />}
      </AppDiv>
    );
  }
}

App.propTypes = {
  // props
  items: array.isRequired,
  rowHeight: number,
  className: string,
  show: string,
  // actions
  removeWidget: func.isRequired,
  updateLayout: func.isRequired,
};
const mapStateToProps = state => getDashboard(state);

const mapDispatchToProps = dispatch => bindActionCreators({
  removeWidget,
  updateLayout,
}, dispatch);
export default connect(mapStateToProps, mapDispatchToProps)(App);
