import React, { Component } from 'react';
import styled from 'styled-components';
import {
  FormCheck,
  FormCheckInput,
} from 'styled-bootstrap-components';
import widgetsJSON from './widgets.json';

const WidgetChoiceStyle = styled.div`
    & label > div {
        display: flex;
        align-items: center;
        .imgContainer  {
            width: 10vw;
            height: 10vh;
            background-size: contain;
            background-repeat: no-repeat;
            background-position: 50% 50%;
            background-color: black;
            img {
                width: 100%;
            }
        }
        .text {
            margin-left: 10px;
        }
    }
    & label > div:nth-of-type(odd) { 
        background: rgba(0,0,0,.2); 
        margin: 20px 0px;
    } 
    & label > div:nth-of-type(even) { 
        background: rgba(0,0,0,.05); 
        margin: 20px 0px;
    } 
    & input[type="checkbox"] {
        opacity: 0;
        position: fixed;
        width: 0;
      
      }
    & input[type="checkbox"]:checked + label {
        background-color:rgba(0,0,255,.3);
        background-color: #007bff;
        border-color: #007bff;
        color: white;
      
      }
    & input[type="checkbox"]:checked + label > div {
        background-color:rgba(0,0,255,.3);
        background-color: #007bff;
        border-color: #007bff;
        border-width: 5px;
        border-style: solid;
        color: white;
      
    }
`;
export default class Widget extends Component {
  render() {
    const { input: { value, onChange } } = this.props;
    return (
      <WidgetChoiceStyle>
        {widgetsJSON.widgets.map(({ logo, text, url }) => (
          <FormCheck>
            <FormCheckInput
              type="checkbox"
              id={text}
              onChange={(event) => {
                const arr = [...value];
                if (event.target.checked) {
                  arr.push(text);
                } else {
                  arr.splice(arr.indexOf(text), 1);
                }
                return onChange(arr);
              }}
              checked={value.includes(text)}

            />
            <label htmlFor={text}>
              <div>
                <div className="imgContainer" style={{ backgroundImage: `url("${logo}")` }} />
                <div className="text">
                  {text}
                </div>
              </div>
            </label>
          </FormCheck>
        ))}
      </WidgetChoiceStyle>
    );
  }
}
